@use "../abstract" as *;

.fa {
    @include positionCenter(gridCenter);
    // border: 1px solid black;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    color: hotpink;
    transition: all .2s ease-in-out;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -ms-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
}
.fa-facebook {
    &:hover {
        color: blue
    }
}
.fa-whatsapp {
    &:hover {
        color: green
    }
}
.fa-instagram {
    &:hover {
        //animation: name duration timing-function delay iteration-count direction fill-mode;
        animation: instagram 2s ease infinite;
        -webkit-animation: instagram 2s ease infinite;
}
}
@keyframes instagram {
    from {
        color: hotpink;
    }
    to {
        color: purple;
    }
}
.fa-twitter {
    &:hover {
        color: black
    }
}
.fa-envelope {
    &:hover {
        color: red
    }
}
